import Vue from 'vue'

import common from './comfirm.vue'

const VueComponent = Vue.extend(common)
const vm = new VueComponent().$mount()

//  

const init = false

const defaultBtn = {
    title: '',
    content: '',
    cancelBtn: '',//
    confirmBtn: '',///
    contentCss:'',//content{'color':'#000000'}
    confirmBtnCss: ''
}
const comfirm = function (options) {
    let jsonStr = {}
    if (typeof (options) != 'object') {
        jsonStr.content = options
    } else {
        jsonStr = options
    }
    // confirmbtn,options

    Object.assign(vm, defaultBtn, jsonStr)

    document.body.appendChild(vm.$el)

    // if(init)append

    return vm.comfirm(options)

}

export default comfirm