import wx from "weixin-js-sdk";
let paramJson = {
  path: "api-server/auth/getTicket",
  url: location.href.split("#")[0]
};
let configStr = {};
Vue.prototype.formRequest(paramJson, res => {
  if (res.data.code == 0) {
    let data = res.data.data;
    configStr = {
      debug: false, // ,apialertpclogpc
      appId: data.appId, // 
      timestamp: data.timestamp, // 
      nonceStr: data.nonceStr, // 
      signature: data.signature, // 
      jsApiList: ["onMenuShareAppMessage", "onMenuShareTimeline"] // JS
    };
    wx.config(configStr);
    wx.ready(() => {
      initShareInfo(wx);
      // wx.hideMenuItems({
      //   menuList: ["menuItem:copyUrl"] // 
      // });
    });
  }
});

function initShareInfo(wx) {
  let shareInfo = {}
  if (location.href.indexOf("publicPage") >= 0) {
    shareInfo = {
      title: "-", // 
      desc: "", // 
      link: "https://wx.liaoxiaoliao.com/publicPage/index.html", // JS
      imgUrl: "https://wx.liaoxiaoliao.com/publicPage/liaoLogo.png" // 
    };
  } else {
    shareInfo = {
      title: "-", // 
      desc: "", // 
      link: "http://wx.qmcai.com/liaoxiaoliao/index.html", // JS
      imgUrl: "http://wx.qmcai.com/liaoxiaoliao/liaoLogo.png" // 
    };
  }
  wx.onMenuShareAppMessage(shareInfo);// 
  wx.onMenuShareTimeline(shareInfo);// 
}

function createApp() {
  new Vue({
    router,
    store,
    render: function (createElement) {
      return createElement(App);
    },
    mounted() {
      this.onpopstate()
    },
    created() {

      // let docEl = document.documentElement,
      //   resizeEvt = 'orientationchange' in window ? 'orientationchange' : 'resize',
      //   recalc = () => {

      //     var clientWidth = docEl.clientWidth;
      //     this.$store.state.screenWidth = this.$util.getCookie("screenWidth")?this.$util.getCookie("screenWidth"):docEl.clientWidth; //
      //     this.$store.state.screenHeight = docEl.clientHeight; //
      //     this.$util.setCookie("screenWidth", this.$store.state.screenWidth, true);
      //     if (!this.$store.state.screenWidth) return;
      //     docEl.style.fontSize = 100 * (this.$store.state.screenWidth / 750) + 'px';
      //     var sUserAgent = navigator.userAgent.toLowerCase();
      //     var bIsIpad = sUserAgent.match(/ipad/i) == "ipad";
      //     var bIsIphoneOs = sUserAgent.match(/iphone os/i) == "iphone os";
      //     var bIsMidp = sUserAgent.match(/midp/i) == "midp";
      //     var bIsUc7 = sUserAgent.match(/rv:1.2.3.4/i) == "rv:1.2.3.4";
      //     var bIsUc = sUserAgent.match(/ucweb/i) == "ucweb";
      //     var bIsAndroid = sUserAgent.match(/android/i) == "android";
      //     var bIsCE = sUserAgent.match(/windows ce/i) == "windows ce";
      //     var bIsWM = sUserAgent.match(/windows mobile/i) == "windows mobile";
      //     if (bIsIphoneOs || bIsMidp || bIsUc7 || bIsUc || bIsAndroid || bIsCE || bIsWM) {

      //     } else if (bIsIpad) {
      //       docEl.style.fontSize = 90 + 'px';
      //     } else {
      //       //pc
      //       docEl.style.fontSize = 50 + 'px';
      //     }
      //   };
      // if (!document.addEventListener) return;
      // window.addEventListener(resizeEvt, recalc, false);
      // recalc();
      axios.defaults.retry = 1; //
      axios.defaults.retryDelay = 1000;//
      axios.defaults.shouldRetry = (error) => true;//
      //ajax
      axios.interceptors.request.use((config) => {
        document.getElementById('buffer').style.display = 'block'
        return config
      }, function (error) { // 
        return Promise.reject(error)
      });
      axios.interceptors.response.use((response) => {
        document.getElementById('buffer').style.display = 'none'
        return response
      }, function (err) {
        // debugger
        document.getElementById('buffer').style.display = 'none'
        let config = err.config;
        // 
        if (!config || !config.retry) return Promise.reject(err);
        if (!config.shouldRetry || typeof config.shouldRetry != 'function') {
          return Promise.reject(err);
        }
        //
        if (!config.shouldRetry(err)) {
          return Promise.reject(err);
        }
        // 0
        config.__retryCount = config.__retryCount || 0;
        // 
        if (config.__retryCount >= config.retry) {
          return Promise.reject(err);
        }
        //
        config.__retryCount += 1;
        //
        let backoff = new Promise(function (resolve) {
          setTimeout(function () {
            resolve();
          }, config.retryDelay || 1);
        });
        //axios
        return backoff.then(function () {
          return axios(config);
        });
      })
    },
    methods: {
      // , 
      onpopstate() {
        this.$preview.on('imageLoadComplete', (e, item) => {
          let _preview = this.$preview.self;
          let lookUrl = window.location.href + '&look';
          window.history.pushState(null, null, lookUrl);
          _preview.listen('close',
            function () {
              if (window.location.href.indexOf('&look') !== -1) {
                console.log("");
                window.history.back();
              }
            });
          window.onhashchange = function () {
            if (_preview !== null && _preview !== undefined) {
              _preview.close();
              _preview = null;
            }
          };
        });
      }
    },
  }).$mount('#app')
}
